import React from 'react';
import { RenderMarkProps, Controller } from '@ali/4ever-cangjie';
import { CommentPluginConfigs } from '../types';
import { MarkView } from '../components/markView';
import { ViewMark } from '../models/marks';

export default (configs: CommentPluginConfigs | undefined) => {
  return function renderMark(
    props: RenderMarkProps,
    controller: Controller,
    next,
  ) {
    if (configs?.isDisabled && configs.isDisabled()) {
      return next();
    }
    const { mark } = props;
    if (ViewMark.isViewMark(mark)) {
      return (
        <MarkView
          controller={controller}
          mark={mark}
          showInline={configs?.showInline}
          customClick={configs?.onBubbleClick}
        >
          {next()}
        </MarkView>
      );
    }
    return next();
  };
};
